
import { Component, Vue } from 'vue-property-decorator';
import Torus, { UserInfo } from '@toruslabs/torus-embed';

@Component({
  components: {

  },
})
export default class Home extends Vue {
    private pk = '';

    private userInfo!: UserInfo;

    public mounted():void {
      this.pk = this.$route.params.pk;
      this.userInfo = JSON.parse(this.$route.params.userInfo) as UserInfo;
      this.userInfo.isNewUser = false;
      this.loginWalletWithPk();
    }

    private async loginWalletWithPk():Promise<void> {
      const torus = new Torus();
      await torus.init();
      await torus.loginWithPrivateKey(
        { privateKey: this.pk, userInfo: this.userInfo },
      );
      // this.userInfo = await torus.getUserInfo('');
      // console.log(this.userInfo);
    }
}
